video{
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container{
/* background: url('images/img-home.jpg') center center/cover no-repeat; */
height: 100vh;
width: 100%;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
object-fit: contain;
}

.hero-container > h1{
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
    /* font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; */
}


.hero-container > p{
    color: #fff;
    font-size: 32px;
    margin-top: 8px;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.hero-btns{
    margin-top: 32px;
}

.btn{
    margin: 6px;
}

.fa-play-circle{
    margin-left: 4px;
}


@media screen and (max-width: 960px){
    .hero-container > h1{
        color: #fff;
        font-size: 70px;
        margin-top: -150px;
        /* font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; */
    }
    
}


@media screen and (max-width: 786px){
    .hero-container > h1{
        color: #fff;
        font-size: 50px;
        margin-top: -100px;
        /* font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; */
    }

    .hero-container p{
        font-size: 30px;
    }
    .btn-mobile{
        display: block;
        text-decoration: none;
    }
    .btn{
        width: 100%;
    }
}



@media screen and (max-width: 570px){
    .hero-container > h1{
        color: #fff;
        font-size: 24px;
        margin-top: -100px;
        /* font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; */
    }

    .hero-container p{
        font-size: 19px;
    }
    
}